@use 'scss/util' as *;

.App {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .container {
    width: rem(500);
    display: flex;
    flex-direction: column;
    align-items: center;

    h1 {
      text-align: center;
    }

    .card {
      width: 100%;
      padding: rem(10);
      border-radius: rem(10);
      border: hsla(0, 0%, 0%, .2) solid rem(.5);
      display: flex;
      flex-direction: column;
      -webkit-box-shadow: rem(0) rem(7) rem(24) rem(3) rgba(0, 0, 0, 0.33);
      box-shadow: rem(0) rem(7) rem(24) rem(3) rgba(0, 0, 0, 0.33);

      &__input {
        display: flex;
        align-items: center;

        svg {
          transition: .3s ease;
          cursor: pointer;
          font-size: rem(30);
        }

        input {
          width: 100%;
        }

        .MuiFormControl-root {
          margin-left: rem(5);
        }
      }

      &__todos {
        display: flex;
        border-bottom: hsla(0, 0%, 0%, .2) solid rem(.5);

        &-done {
          text-decoration: line-through;
          color: hsla(0, 0%, 0%, .2);
        }

        &-collapse {
          :nth-child(n+4) {
            display: none;
          }
        }

        p {
        }
      }

      &__manager {
        $self: &;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 0 rem(15);

        p {
          color: hsla(0, 0%, 0%, .4);
          cursor: pointer;
        }

        h5 {
          color: hsla(0, 0%, 0%, .4);
        }

        &--tabs {
          display: flex;
          gap: rem(5);
          line-height: rem(20);
        }

        &--tab-active {
          border: rem(.5) solid hsla(0, 0%, 0%, .2);
          border-radius: rem(5);
        }

        &--tab {
          border: rem(.5) solid hsla(0, 0%, 0%, 0);
        }
      }
    }
  }
}
